function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        speed: 600,
        loop: true,
        autoplay: {
            delay: 3000
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function sitesSlider() {
    var swiper = new Swiper('.sites .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        breakpoints: {
            320: {
                spaceBetween: 50,
            },
            992: {
                spaceBetween: 95,
            },
        }
    })
}

let SlidersArray = []

function Sliders() {
    $(".slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 30,
            scrollbar: {
                el: `.slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
            },
            breakpoints: {
                320: {
                    spaceBetween: 10,
                },
                992: {
                    spaceBetween: 30,
                },
            }
        })
    })
}


mainSlider()
Sliders()
sitesSlider()
